import React, { useState, useRef, useEffect } from 'react';
import { 
    Box, Typography, Button, useMediaQuery, TextField, Paper, 
    Avatar, Divider, CircularProgress, Fade, 
} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import SmartToyIcon from '@mui/icons-material/SmartToy';
import PersonIcon from '@mui/icons-material/Person';
import LightbulbIcon from '@mui/icons-material/Lightbulb';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import { useCalendar } from '../../contexts/CalendarContext';
import theme from '../../theme/theme';

function AISection() {
    const { aiAdvice, loading, selectedLocation, selectedAction, selectedYear, selectedMonth } = useCalendar();
    const isMobile = useMediaQuery('(max-width:768px)');
    const [followupQuestion, setFollowupQuestion] = useState('');
    const [conversation, setConversation] = useState([]);
    const [isTyping, setIsTyping] = useState(false);
    const messagesEndRef = useRef(null);
    const chatContainerRef = useRef(null);
    
    // AI
    useEffect(() => {
        if (aiAdvice && !conversation.some(msg => msg.type === 'ai' && msg.text === aiAdvice)) {
            setConversation([
                { type: 'ai', text: aiAdvice }
            ]);
        }
    }, [aiAdvice, conversation]);
    
    // 
    /*
    const [shouldScrollToBottom, setShouldScrollToBottom] = useState(false);
    
    // 
    useEffect(() => {
        // 
        if (shouldScrollToBottom && messagesEndRef.current && chatContainerRef.current) {
            // 
            const timer = setTimeout(() => {
                // 
                const container = chatContainerRef.current;
                const isNearBottom = container.scrollHeight - container.scrollTop - container.clientHeight < 150;
                const lastMsg = conversation[conversation.length - 1];
                
                if (isNearBottom || lastMsg?.type === 'user' || conversation.length <= 1) {
                    messagesEndRef.current.scrollIntoView({ 
                        behavior: 'smooth', 
                        block: 'end'
                    });
                }
                // setShouldScrollToBottom(false);
            }, 100);
            
            return () => clearTimeout(timer);
        }
    }, [conversation, shouldScrollToBottom]);
    */
    
    // 
    const handleAskQuestion = async () => {
        if (!followupQuestion.trim()) return;
        
        const userQuestion = followupQuestion.trim();
        
        // 
        setConversation(prev => [...prev, { type: 'user', text: userQuestion }]);
        setFollowupQuestion('');
        setIsTyping(true);
        // 
        // setShouldScrollToBottom(true);  // 
        
        try {
            // api
            // 
            setTimeout(() => {
                const aiResponse = `${userQuestion}\n\n${selectedMonth || "X"}${Math.round(Math.random() * 20) + 10}%${selectedAction?.includes('event') ? '' : ''}\n\n`;
                
                // AI
                setConversation(prev => [...prev, { type: 'ai', text: aiResponse }]);
                setIsTyping(false);
                // 
                // setShouldScrollToBottom(true);  // AI
            }, 1500);
            
            // API
            /*
            const response = await fetch('/api/ask-ai', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    question: userQuestion,
                    location: selectedLocation,
                    action: selectedAction,
                    year: selectedYear,
                    month: selectedMonth,
                    context: conversation
                })
            });
            
            if (!response.ok) throw new Error('AI');
            
            const data = await response.json();
            setConversation(prev => [...prev, { type: 'ai', text: data.response }]);
            setShouldScrollToBottom(true);
            */
            
        } catch (error) {
            // 
            setConversation(prev => [...prev, { 
                type: 'ai', 
                text: '',
                isError: true 
            }]);
            // 
            // setShouldScrollToBottom(true);
        } finally {
            setIsTyping(false);
        }
    };
    
    const handleKeyPress = (e) => {
        if (e.key === 'Enter' && !e.shiftKey) {
            e.preventDefault();
            handleAskQuestion();
        }
    };
    
    // AI
    const renderAIMessage = (text, isError = false) => {
        // 
        const paragraphs = text.split('\n').filter(p => p.trim() !== '');
        
        return (
            <Box>
                {paragraphs.map((paragraph, idx) => (
                    <Typography 
                        key={idx} 
                        variant="body1" 
                        color={isError ? "error" : "textPrimary"}
                        sx={{ 
                            mb: 1.5, 
                            lineHeight: 1.6,
                            fontSize: '0.95rem',
                            whiteSpace: 'pre-wrap'
                        }}
                    >
                        {paragraph}
                    </Typography>
                ))}
            </Box>
        );
    };
    
    // 
    const renderEmptyState = () => {
        if (loading) {
            return (
                <Box sx={{ 
                    display: 'flex', 
                    flexDirection: 'column', 
                    alignItems: 'center', 
                    justifyContent: 'center',
                    p: 3,
                    minHeight: '180px'
                }}>
                    <CircularProgress size={36} color="primary" sx={{ mb: 2 }} />
                    <Typography variant="body1" align="center" color="textSecondary">
                        AI...
                    </Typography>
                </Box>
            );
        }
        
        if (!selectedLocation || !selectedAction || !selectedYear || !selectedMonth) {
            return (
                <Box sx={{ 
                    display: 'flex', 
                    flexDirection: 'column', 
                    alignItems: 'center', 
                    justifyContent: 'center',
                    p: 3,
                    minHeight: '180px',
                    backgroundColor: 'rgba(245, 245, 245, 0.5)',
                    borderRadius: '12px',
                }}>
                    <InfoOutlinedIcon sx={{ fontSize: 40, color: theme.palette.primary.main, mb: 2, opacity: 0.7 }} />
                    <Typography variant="body1" align="center" color="textSecondary" sx={{ maxWidth: '80%' }}>
                        AI
                    </Typography>
                </Box>
            );
        }
        
        return null;
    };

    return (
        <Paper elevation={2} sx={{ 
            borderRadius: '12px',
            overflow: 'hidden',
            mb: 3,
            border: '1px solid rgba(0, 0, 0, 0.08)'
        }}>
            {/*  */}
            <Box sx={{ 
                display: 'flex', 
                alignItems: 'center', 
                backgroundColor: theme.palette.primary.main,
                color: 'white',
                p: isMobile ? 1.5 : 2,
                borderBottom: '1px solid rgba(0, 0, 0, 0.08)'
            }}>
                <LightbulbIcon sx={{ 
                    mr: 1.5,
                    fontSize: isMobile ? '1.5rem' : '1.75rem',
                    color: theme.palette.primary.contrastText
                }} />
                <Typography 
                    variant="h6" 
                    component="h2" 
                    sx={{ 
                        fontWeight: 600,
                        fontSize: isMobile ? '1.1rem' : '1.2rem'
                    }}
                >
                    AI
                </Typography>
                
            </Box>
            
            {/*  */}
            <Box 
                ref={chatContainerRef}
                sx={{ 
                    p: 0,
                    height: isMobile ? '300px' : '350px',
                    maxHeight: '350px',
                    overflowY: 'auto',
                    backgroundColor: 'rgba(250, 250, 250, 0.5)',
                    // 
                    scrollBehavior: 'auto',
                    '&::-webkit-scrollbar': {
                        width: '8px',
                    },
                    '&::-webkit-scrollbar-track': {
                        background: '#f1f1f1',
                    },
                    '&::-webkit-scrollbar-thumb': {
                        background: '#bbb',
                        borderRadius: '4px',
                    },
                }}
            >
                {conversation.length === 0 ? (
                    renderEmptyState()
                ) : (
                    <Box sx={{ p: 2 }}>
                        {conversation.map((message, index) => (
                            <Box 
                                key={index} 
                                sx={{ 
                                    display: 'flex', 
                                    mb: 2.5,
                                    alignItems: 'flex-start'
                                }}
                            >
                                <Avatar 
                                    sx={{ 
                                        bgcolor: message.type === 'ai' ? theme.palette.primary.main : 'gray',
                                        width: 36,
                                        height: 36,
                                        mr: 1.5
                                    }}
                                >
                                    {message.type === 'ai' ? <SmartToyIcon /> : <PersonIcon />}
                                </Avatar>
                                <Box 
                                    sx={{ 
                                        backgroundColor: message.type === 'ai' 
                                            ? 'white' 
                                            : theme.palette.grey[100],
                                        p: 1.5,
                                        borderRadius: '12px',
                                        flexGrow: 1,
                                        border: message.type === 'ai' ? '1px solid rgba(0, 0, 0, 0.08)' : 'none',
                                        ml: message.type === 'user' ? 'auto' : 0,
                                        maxWidth: '85%'
                                    }}
                                >
                                    {message.type === 'ai' 
                                        ? renderAIMessage(message.text, message.isError) 
                                        : (
                                            <Typography 
                                                variant="body1" 
                                                sx={{ 
                                                    fontSize: '0.95rem',
                                                    fontWeight: 500
                                                }}
                                            >
                                                {message.text}
                                            </Typography>
                                        )
                                    }
                                </Box>
                            </Box>
                        ))}
                        
                        {/*  */}
                        {isTyping && (
                            <Fade in={isTyping}>
                                <Box 
                                    sx={{ 
                                        display: 'flex', 
                                        mb: 2,
                                        alignItems: 'flex-start'
                                    }}
                                >
                                    <Avatar 
                                        sx={{ 
                                            bgcolor: theme.palette.primary.main,
                                            width: 36,
                                            height: 36,
                                            mr: 1.5
                                        }}
                                    >
                                        <SmartToyIcon />
                                    </Avatar>
                                    <Box 
                                        sx={{ 
                                            display: 'flex',
                                            alignItems: 'center',
                                            p: 2,
                                            borderRadius: '12px',
                                            backgroundColor: 'white',
                                            border: '1px solid rgba(0, 0, 0, 0.08)',
                                        }}
                                    >
                                        <Box sx={{ 
                                            display: 'flex', 
                                            alignItems: 'center',
                                            gap: 0.5
                                        }}>
                                            <Box className="loading-pulse" sx={{ 
                                                width: 6, 
                                                height: 6, 
                                                backgroundColor: theme.palette.grey[400],
                                                borderRadius: '50%' 
                                            }} />
                                            <Box className="loading-pulse" sx={{ 
                                                width: 6, 
                                                height: 6, 
                                                backgroundColor: theme.palette.grey[400],
                                                borderRadius: '50%',
                                                animationDelay: '0.2s'
                                            }} />
                                            <Box className="loading-pulse" sx={{ 
                                                width: 6, 
                                                height: 6, 
                                                backgroundColor: theme.palette.grey[400],
                                                borderRadius: '50%',
                                                animationDelay: '0.4s'
                                            }} />
                                        </Box>
                                    </Box>
                                </Box>
                            </Fade>
                        )}
                        
                        {/*  */}
                        <div ref={messagesEndRef} style={{ height: 1 }} />
                    </Box>
                )}
            </Box>
            
            {/*  */}
            <Divider />
            <Box sx={{ 
                p: 2, 
                display: 'flex', 
                alignItems: 'center',
                backgroundColor: 'white'
            }}>
                <TextField 
                    fullWidth
                    variant="outlined"
                    size="small"
                    placeholder={aiAdvice ? "AI..." : ""}
                    value={followupQuestion}
                    onChange={(e) => setFollowupQuestion(e.target.value)}
                    onKeyPress={handleKeyPress}
                    disabled={loading || !aiAdvice || isTyping}
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            borderRadius: '20px',
                            paddingRight: '14px',
                            backgroundColor: theme.palette.grey[50]
                        }
                    }}
                    InputProps={{
                        endAdornment: (
                            <Button
                                color="primary"
                                disabled={!followupQuestion.trim() || loading || !aiAdvice || isTyping}
                                onClick={handleAskQuestion}
                                sx={{ 
                                    minWidth: 'auto', 
                                    borderRadius: '50%',
                                    width: 32,
                                    height: 32,
                                    padding: 0
                                }}
                            >
                                <SendIcon fontSize="small" />
                            </Button>
                        )
                    }}
                />
            </Box>
        </Paper>
    );
}

export default AISection;
