// src/utils/colorPalettes.js
export const COLOR_PALETTE_NAMES = {
    GREEN_TO_RED: '',
    BLUE_TO_RED: '',
    VIRIDIS: 'Viridis',
    VIRIDIS_REVERSE: 'Viridis',
    WHITE_TO_BLUE: '',
    GREEN_YELLOW_RED: '',
    GREEN_YELLOW_RED_ALT: '()'
  };
  
  // 
  export const colorPalettes = {
    // 
    [COLOR_PALETTE_NAMES.GREEN_TO_RED]: (congestion) => {
      if (congestion === 1) return '#e8f5e9'; // 
      if (congestion === 2) return '#c8e6c9'; // 
      if (congestion === 3) return '#a5d6a7'; // 
      if (congestion === 4) return '#fff59d'; // 
      if (congestion === 5) return '#ffe082'; // 
      if (congestion === 6) return '#ffcc80'; // 
      if (congestion === 7) return '#ffab91'; // 
      if (congestion === 8) return '#ef9a9a'; // 
      if (congestion === 9) return '#e57373'; // 
      if (congestion === 10) return '#ef5350'; // 
      return '#FFF';
    },
    
    // 
    [COLOR_PALETTE_NAMES.BLUE_TO_RED]: (congestion) => {
      if (congestion === 1) return '#699ecd';
      if (congestion === 2) return '#83add5';
      if (congestion === 3) return '#9bbfe1';
      if (congestion === 4) return '#b3d1eb';
      if (congestion === 5) return '#cfe4f0';
      if (congestion === 6) return '#fbcacc';
      if (congestion === 7) return '#fa9699';
      if (congestion === 8) return '#f97884';
      if (congestion === 9) return '#f67a80';
      if (congestion === 10) return '#f0545c';
      return '#FFF';
    },
    
    // Viridis
    [COLOR_PALETTE_NAMES.VIRIDIS]: (congestion) => {
      if (congestion === 1) return '#440154'; // 
      if (congestion === 2) return '#482878'; // 
      if (congestion === 3) return '#3e4989'; // 
      if (congestion === 4) return '#31688e'; // 
      if (congestion === 5) return '#26828e'; // 
      if (congestion === 6) return '#1f9e89'; // 
      if (congestion === 7) return '#35b779'; // 
      if (congestion === 8) return '#6ece58'; // 
      if (congestion === 9) return '#b5de2b'; // 
      if (congestion === 10) return '#fde725'; // 
      return '#FFF';
    },
    
    // Viridis
    [COLOR_PALETTE_NAMES.VIRIDIS_REVERSE]: (congestion) => {
      if (congestion === 1) return '#fde725'; // 
      if (congestion === 2) return '#b5de2b'; // 
      if (congestion === 3) return '#6ece58'; // 
      if (congestion === 4) return '#35b779'; // 
      if (congestion === 5) return '#1f9e89'; // 
      if (congestion === 6) return '#26828e'; // 
      if (congestion === 7) return '#31688e'; // 
      if (congestion === 8) return '#3e4989'; // 
      if (congestion === 9) return '#482878'; // 
      if (congestion === 10) return '#440154'; // 
      return '#FFF';
    },
    
    // 
    [COLOR_PALETTE_NAMES.WHITE_TO_BLUE]: (congestion) => {
      switch (congestion) {
        case 10: return '#000000'; // 
        case 9: return '#001433'; // 
        case 8: return '#002766'; // 
        case 7: return '#003A99'; // 
        case 6: return '#004DCC'; // 
        case 5: return '#4A69BD'; // 
        case 4: return '#7F8EBE'; // 
        case 3: return '#B0C4DE'; // 
        case 2: return '#D0E0F0'; // 
        case 1: return '#FFFFFF'; // 
        default: return '#FFF'; // 
      }
    },
    
    // 
    [COLOR_PALETTE_NAMES.GREEN_YELLOW_RED]: (congestion) => {
      if (congestion === 1) return '#e8f5e9'; // 
      if (congestion === 2) return '#c8e6c9'; // 
      if (congestion === 3) return '#a5d6a7'; // 
      if (congestion === 4) return '#fff59d'; // 
      if (congestion === 5) return '#ffe082'; // 
      if (congestion === 6) return '#ffcc80'; // 
      if (congestion === 7) return '#ffab91'; // 
      if (congestion === 8) return '#ef9a9a'; // 
      if (congestion === 9) return '#e57373'; // 
      if (congestion === 10) return '#ef5350'; // 
      return '#FFF';
    },
    
    // 
    [COLOR_PALETTE_NAMES.GREEN_YELLOW_RED_ALT]: (congestion) => {
      if (congestion === 1) return '#f1f8e9'; // 
      if (congestion === 2) return '#dcedc8'; // 
      if (congestion === 3) return '#c5e1a5'; // 
      if (congestion === 4) return '#aed581'; // 
      if (congestion === 5) return '#fff176'; // 
      if (congestion === 6) return '#ffd54f'; // 
      if (congestion === 7) return '#ffb74d'; // 
      if (congestion === 8) return '#ff8a65'; // 
      if (congestion === 9) return '#e57373'; // 
      if (congestion === 10) return '#d32f2f'; // 
      return '#FFF';
    }
  };
